const elements = [
  a = document.querySelector('.s'),
  b = document.querySelector('.d'),
  c = document.querySelector('.v'),
  d = document.querySelector('.b'),
  f = document.querySelector('.n'),
]


const time = 2500;
const step = 1;

function outNum(num, elem) {
  let e = document.getElementById('counter');
  n = 0;
  let t = Math.round(time / (num / step));
  let interval = setInterval(() => {
    n = n + step;
    if (n == num) {
      clearInterval(interval);
    }
    e.innerHTML = n;
  }, t);
}

outNum(260, "#counter");



window.onscroll = function () { 
  scrollFunction();
};
function  scrollFunction() {
   let scrollPos = 150;
   let menu = document.querySelector('.header__nav')
   let header = document.getElementById('header')
   let phoneNumber = document.querySelector('.header__phone-link')

   if(document.body.scrollTop > scrollPos || document.documentElement.scrollTop > scrollPos) {
     header.classList.add('fixed');
     menu.classList.add('resize');
     phoneNumber.classList.add('resize');
   } else {
     header.classList.remove('fixed');
     menu.classList.remove('resize');
     phoneNumber.classList.remove('resize');
   }
}


//animation

const animItems = document.querySelectorAll('.anim_item');

if (animItems.length > 0) {
  window.addEventListener('scroll', animOnScroll);
  function animOnScroll() {
    for (let index = 0; index < animItems.length; index++) {
      const animItem = animItems[index];
      const animItemHeight = animItem.offsetHeight;
      const animItemOffset = offset(animItem).top;
      const animStart = 100;

      let animItemPoint = window.innerHeight - animItemHeight / animStart;
      if (animItemHeight > window.innerHeight) {
        animItemPoint = window.innerHeight - window.innerHeight / animStart;
      }
      if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
        animItem.classList.add('_active');
      } else {
        if (!animItem.classList.contains('_no_hide')){
          animItem.classList.remove('_active');
        }
      }
    }
  }
  function offset(el) {
    const rect = el.getBoundingClientRect(),
      scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
      scrollTop = window.pageYOffset || document.documentElement.scrollTop;
    return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
  }


  setTimeout(() => {
    animOnScroll();
  }, 0);
}